body {
  overflow-x: hidden;
  background-image: url(../img/bg.jpg);
  background-size: contain;
}

//header

.centered-container {
  width: 100%;
  max-width: 1280px;
  display: block;
  margin: 0 auto;
  padding: 0px 15px;
}

.header-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

header#nav {
  position: relative;
  z-index: 100;
  width: 100%;
  padding: 10px 0;
  background-color: #fff;
}



#logo {
  position: relative;
  z-index: 123;
  padding: 10px;
  color: #6DDB07;
}




header#nav #menuWrapper {
  height: auto;
  display: block;
}

header#nav .menu-head {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: inline-block;
}

header#nav .menu-head > li {
  float: left;
  margin-right: 30px;
  padding: 10px 0;
}

.buttons-wrapp {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  a {
    padding: 15px 40px;
    text-align: center;
    margin: 10px;
    border-radius: 46px;
    font-family: "DM Sans", sans-serif;
    font-size: 23px;
    font-style: normal;
    font-weight: 700;
    text-decoration: none;
    line-height: 32.4px; /* 140.87% */
    &:last-of-type {
      background: #1D1D21;
      color: #fff;
    }

    &:first-of-type {
      background: #EBB200;
      color: #000;
    }
  }
}

header#nav .menu-head > li > a {
  color: #191818;
  font-family: "DM Sans", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  text-decoration: none;
  outline: none;
  line-height: 140%; /* 22.4px */
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -ms-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
}



header#nav .menu-wrapp .menu-head > li > ul {
  position: absolute;
  left: 10px;
  opacity: 0;
  min-width: 200px;
  margin: 0;
  pointer-events: none;
  /* Adding a transition timing on here will cause mouse out bugs after hover! */
}

header#nav .menu-wrapp .menu-head > li > ul > li {
  padding: 0;

  float: left;
  display: block;
  width:100%;
}

header#nav .menu-wrapp .menu-head > li > ul > li > a {
  border-bottom: 0;
  display: block;
  padding: 0 5px;
  color: #fff;
  width: 90%;
  padding: 0 5%;
  text-transform:none;
}

/* Fade = no need for animations - just timing effects... */

.ddFade {
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.ddFadeFast {
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.ddFadeSlow {
  -webkit-transition: all 1s ease-in-out;
  -moz-transition: all 1s ease-in-out;
  -ms-transition: all 1s ease-in-out;
  -o-transition: all 1s ease-in-out;
  transition: all 1s ease-in-out;
}

/* General Animation settings */
header#nav .menu-wrapp .menu-head > li > ul {
  opacity:0;
  display:block;
  perspective:1000px;
  min-width:inherit;
  width:200px;
}

header#nav .menu-wrapp .menu-head > li > ul.dropin li {
  opacity:1;
  display:block;
  width:100%;
  background:#333;
  padding:3px;
  display:block;
  float: left;
}

li:hover ul.hov {
  animation-duration: 0.3s;
  animation-delay: 0.3s;
}

li:hover ul.hov li {
  animation-direction: normal;
  animation-iteration-count:1;
  animation-timing-function : ease-in-out;
  background:#333;
  animation-fill-mode: forwards;
}

/* Drop In */

/* Swing In right */
header#nav .menu-wrapp .menu-head > li > ul.swing {
  transform: translate(-50px,0) rotateY(-90deg);
}
li:hover ul.swing.hov {
  animation-name: swing;
  animation-fill-mode: forwards;
}
@keyframes swing {
  0%   {opacity:0; transform: translate(-50px,0) rotateY(-90deg);}
  100% {opacity:1; transform: translate(0,0) rotateY(0);}
}
@-webkit-keyframes swing {
  0%   {opacity:0; transform: translate(-50px,0) rotateY(-90deg); }
  100% {opacity:1; transform: translate(0,0) rotateY(0);}
}

/* Swing In Left */
header#nav .menu-wrapp .menu-head > li > ul.swingLft {
  transform: translate(50px,0) rotateY(90deg);
}
li:hover ul.swingLft.hov {
  animation-name: swingLft;
  animation-fill-mode: forwards;
}
@keyframes swingLft {
  0%   {opacity:0; transform: translate(50px,0) rotateY(90deg);}
  100% {opacity:1; transform: translate(0,0) rotateY(0);}
}
@-webkit-keyframes swingLft {
  0%   {opacity:0; transform: translate(50px,0) rotateY(-0deg); }
  100% {opacity:1; transform: translate(0,0) rotateY(0);}
}

/* sort all the overing, going on... */
header#nav .menu-head li:hover a {
  color: #e10600;
}
header#nav .menu-wrapp .menu-head > li:hover ul {
  opacity:1;
  top:24px;
  pointer-events: auto;
}
header#nav .menu-wrapp .menu-head > li > ul .ico:hover,
header#nav .menu-wrapp .menu-head > li > ul a:hover { background-color: rgba(0,0,0,0.3); }
header#nav .menu-wrapp .menu-head > li:hover ul { top :45px; }

/* A non image based mobile menu and close button */
#hamburger {
  display: none;
  width: 25px;
  height: 24px;
  position: absolute;
  right: 15px;
  top: 26px;
  cursor: pointer;
}

#hamburger > span {
  background: #000;
  display: block;
  width: 100%;
  height: 3px;
  position: relative;
  margin-top: 3px;
  color: #fff;
  -webkit-transition:all .3s ease;
  -moz-transition:all .3s ease;
  -ms-transition:all .3s ease;
  -o-transition:all .3s ease;
  transition:all .3s ease;
}

#close {
  position: fixed;
  top: 26px;
  right: 15px;
  width: 25px;
  height: 25px;
  z-index: 1200;
  display: none;
  cursor: pointer;
}

#close > span, #close > span::after {
  content: "";
  display: block;
  width: 4px;
  height: 100%;
  background: #eee;
  position: absolute;
  left: 50%;
  margin-left: -2px;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}
#close > span::after {
  -webkit-transform: rotate(-90deg);
  -moz-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  -o-transform: rotate(-90deg);
  transform: rotate(-90deg);
}
#close:hover > span,
#close:hover > span::after { background: #ccc; }
#hamburger:hover > span,
#hamburger:hover > span::after,
#hamburger:hover > span::before {
  background: rgba(250,250,250,0.5);
}

